export const shakeArray = arr => {
  return arr.sort(() => Math.random() - 0.5);
};

export const updateState = (oldState, updates) => {
  return {
    ...oldState,
    ...updates
  };
};

export const processJSONfromGoogleSpreadsheet = data => {
  data = data.feed.entry;
  let arr = [];
  data.forEach((elem, index) => {
    arr.push({});
    arr[index].word = elem.gsx$word.$t.toUpperCase();
    arr[index].translation = elem.gsx$translation.$t.toUpperCase();
    arr[index].usage = elem.gsx$usage.$t.toLowerCase();
    arr[index].img = elem.gsx$img.$t;
    arr[index].shakenArray = shakeArray(
      elem.gsx$word.$t.toUpperCase().split("")
    );
  });
  return arr;
};



